import { initAccordion } from '../objects/accordion';
import { getElems, timer } from '../tools/utilities/helper-functions';

const CLASS_FADE_IN = 'is-fade-in';
const CLASS_FADE_OUT = 'is-fade-out';
const CLASS_IS_VISIBLE = 'is-visible';
const CLASS_IS_ACTIVE = 'is-active';

type Category = 'productsFaqs' | 'paymentFaqs' | 'deliveryFaqs' | 'returnsFaqs';

const accordions = getElems('[data-accordion-container]');
const categoryButtons = getElems('[data-category]');

accordions.forEach(initAccordion);

onCategorySelected((selectedCategory: Category) => {
  showSelectedAccordion(selectedCategory);
  setCategoryButtonToActive(selectedCategory);
});

/* Implementation */

function onCategorySelected(action: (selectedCategory: Category) => void) {
  categoryButtons.forEach((button) =>
    button.addEventListener('click', () => {
      const category = button.getAttribute('data-category') as Category;
      action(category);
    })
  );
}

function showSelectedAccordion(selectedCategory: Category) {
  accordions.forEach((accordion) => {
    const category = accordion.getAttribute('data-accordion-container');
    if (category == selectedCategory) {
      fadeIn(accordion);
    } else {
      fadeOut(accordion);
    }
  });
}

function setCategoryButtonToActive(selectedCategory: Category) {
  categoryButtons.forEach((button) => {
    const category = button.getAttribute('data-category') as Category;
    if (category == selectedCategory) {
      button.classList.add(CLASS_IS_ACTIVE);
    } else {
      button.classList.remove(CLASS_IS_ACTIVE);
    }
  });
}

async function fadeIn(accordion: HTMLElement) {
  await timer({ seconds: 0.3 });
  accordion.classList.add(CLASS_FADE_IN, CLASS_IS_VISIBLE);
  await timer({ seconds: 0.01 });
  accordion.classList.remove(CLASS_FADE_IN);
}
async function fadeOut(accordion: HTMLElement) {
  accordion.classList.add(CLASS_FADE_OUT);
  await timer({ seconds: 0.3 });
  accordion.classList.remove(CLASS_FADE_OUT, CLASS_IS_VISIBLE);
}
